body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #404040;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.banner {
  padding: 30px 20px;
  text-align: center;
}
.banner h1 {
  margin: 0;
  line-height: 40px;
  font-weight: normal;
  color: #248DF7;
  padding-bottom: 5px;
}
.banner p {
  margin: 0;
  line-height: 18px;
  color: #999999;
}

.am-accordion {
  margin-bottom: 15px;
}
.am-accordion .am-accordion-item {
  border-radius: 2px;
  overflow: hidden;
}
.am-accordion .am-accordion-item .am-accordion-header {
  padding-top: 15px;
  padding-bottom: 15px;
  color: #404040;
  font-size: 16px;
}
html:not([data-scale]) .am-accordion::before,
html:not([data-scale]) .am-accordion .am-accordion-item .am-accordion-header::after {
  display: none;
}
.am-accordion .am-accordion-item .am-accordion-header i {
  top: 30px;
}

.am-list-item .am-list-line .am-list-content {
  font-size: 14px;
  color: #404040;
}

.am-list-item .am-list-line .am-list-content a {
  display: inline-block;
  width: 100%;
  height: 100%;
  color: #404040;
}
ul, p {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}

.am-card {
  min-height: auto;
}
.am-card-header {
  font-size: 15px;
  font-weight: bold;
}
.am-card-header .am-card-header-content {
  color: #2992E3;
}
.am-card-body {
  min-height: auto;
  padding-bottom: 9px;
}

.base-page {
  width: 100%;
  height: 100%;
}
.base-info ul li{
  font-size: 16px;
  line-height: 20px;
  padding: 0 0 8px 0;
}
.base-info ul li:last-child{
  padding-bottom: 0;
}

.icon-edit {
  color: #2992E3;
  font-style: normal;
  margin-left: 10px;
  padding: 5px 0 5px 18px;
  background: url(data:image/png;base64,) 0 center no-repeat;
  background-size: 14px;
  font-size: 14px;
}
.icon-copy {
  color: #2992E3;
  font-style: normal;
  margin-left: 10px;
  padding: 5px 0 5px 18px;
  background: url(data:image/png;base64,) 0 center no-repeat;
  background-size: 15px;
  font-size: 14px;
}

#eidt-params {
  width: 95%;
  min-height: 60px;
  outline: none;
  border: none;
  border: 1px solid #bebebe;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  font-size: 20px;
  color: #2992E3;
  padding: 6px;
}

.tips {
  font-size: 13px;
  color: #C74E2C;
}
